import Vue from 'vue'
import Router from 'vue-router'
import child1 from '../components/child1'
import child2 from '../components/child2'

Vue.use(Router)

export default new Router({
  linkActiveClass:'active',
  routes: [
    {
      path: '/child1',
      name: 'child1',
      component: child1
    },
    {
      path: '/child2',
      name: 'child2',
      component: child2
    }
  ]
})



