<template>
    <div>
      <div>接受父值 => {{msg}}</div>
      <div><label for="cMsg">向父传值 => </label><input id="cMsg" v-model="childMsg"></div>
      <button class="btn" @click="send">向父传值</button>
      <div><label for="pMsg">向同级组件传值 => </label><input id="pMsg" v-model="otherMsg"></div>
      <button class="btn" @click="sendTo1">向同级传值</button>
    </div>
</template>

<script>
    export default {
      props:['msg'],
      data(){
        return {
          otherMsg:'This is send',
          childMsg:'This is child'
        }
      },
      methods:{
        send(){
          this.$emit('cm',this.childMsg);
        },
        sendTo1() {
          this.$root.Bus.$emit('sendMsg', this.otherMsg);
        }
      }
    }
</script>

<style scoped>
  *{margin: 0;padding: 0}
  .btn{
    border:1px solid #222;
    cursor: pointer;
    background: white;
    width:70px;
    padding: 8px 12px;
    height: 50px;
    transition: all .2s linear;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .btn:hover{
    color: white;
    box-shadow:0 0 1px 25px #222 inset;
  }
</style>



