<template>
  <div>
    <div>接受同级值 => {{receiveMsg}}</div>
    </div>
</template>

<script>
    export default {
      data(){
        return {
          receiveMsg:''
        }
      },
      created(){
        let _this=this;
        this.$root.Bus.$on('sendMsg',function(value){
          _this.receiveMsg=value;
        })
      }
    }
</script>

<style scoped>

</style>



