<template>
  <div id="app">
    <div><label for="pMsg">向子传值 => </label><input id="pMsg" v-model="parentMsg"></div>
    <div>接受子值 => {{childMsg}}</div>
    <div><v-send @cm="receive" :msg="parentMsg"></v-send></div>
    <div><v-send1></v-send1></div>
    <router-link :to="{ name:'child1', params: {num: 1111} }">child1</router-link>
    <router-link :to="{ name:'child2', params: {num: 2222} }">child2</router-link>
    <keep-alive>
      <transition name="vis" mode="out-in">
        <router-view/>
      </transition>
    </keep-alive>
  </div>
</template>

<script>
  import send from './components/send'
  import send1 from './components/send1'
export default {
  components:{'v-send':send,'v-send1':send1},
  data(){
    return {
      parentMsg:'This is parent',
      childMsg:''
    }
  },
  methods:{
    receive(msg){
      this.childMsg=msg;
    }
  }
}
</script>

<style>
  a{text-decoration: none;font-size:50px;}
  #app>div{margin: 10px;position: relative}
  #app>div:before{content: '';width: 6px;height: 100%;background: silver;position: absolute;margin-left: -6px}
  .active{color: #9cc;}
  .vis-enter-active, .vis-leave-active{
    transition: all 0.3s ease-out
  }
  .vis-enter, .vis-leave-to{
       transform: translateX(-10%);
       opacity: 0
  }
  .vis-leave, .vis-enter-to{
    opacity: 1
  }
</style>



